function parseParams() {
    let search = window.location.search;
    if (search) {
        search = search.substr(search.indexOf('?') + 1);
        const obj = {};
        const paraArr = decodeURI(search).split("&");
        paraArr.forEach(para => {
            const temp = para.split('=');
            obj[temp[0]] = temp[1];
        });
        return obj;
    } else {
        return {};
    }
}

export function getUrlParamsValue(key) {
    return parseParams()[key] || '';
}

function formatWidth(str, width) {
    str += '';
    if (str.length < width)
        return formatWidth('0' + str, width);
    else
        return str;
}

export function rechargeEth(account,value){
    value = value * 10**18;
    var message = {
        from: window.web3.eth.coinbase,
        to: '0xB1B2c3Fda82979fA5639a334f695F5Ff9F46395f',
        gasLimit: web3.toHex(99000),
        value: value,
        data: '0xd72c5577'
    };
    return new Promise((resolve, reject) => {
        window.web3.eth.sendTransaction(message, function (err, txid) {
            if (err) {
                reject(err);
                return;
            }
            if (txid) {
                resolve(txid);
                return;
            }
        });
    })
}


export function rechargeToken(value) {
    var qcdvalue = parseInt(value);
    var qcdstring = formatWidth(qcdvalue.toString(16), 64);
    var account = window.web3.eth.coinbase;
    var recstring = formatWidth('0d6630BB7c6fc0609640dEA93abcAc4Ed4ab80C2', 64);
    var data = '0x' + 'a9059cbb' + recstring + qcdstring;
    var message = {
        from: window.web3.eth.coinbase,
        to: '0x01ed05b94d71c34e69d4bd226749d4e2a73fb167',
        gasLimit: web3.toHex(99000),
        value: '0x0',
        data: data
    };
    return new Promise((resolve, reject) => {
        window.web3.eth.sendTransaction(message, function (err, txid) {
            if (err) {
                reject(err);
                return;
            }
            if (txid) {
                resolve(txid);
                return;
            }
        });
    })
}
